import { Header } from "@/components/navigation/header";
import { Sidebar } from "@/components/navigation/sidebar";
import { TagBar } from "@/components/navigation/tag-bar";
import { CustomTable } from "@/components/table";
import { ENDPOINTS } from "@/shared/site";
import { cookies } from "next/headers";

async function getData() {
  const cookieStore = await cookies();
  const token = cookieStore.get("access_token")?.value;
  const res = await fetch(`${ENDPOINTS.http_server_base}${ENDPOINTS.profile}`, {
    // headers: {
    //   Authorization: `Bearer ${token}`,
    //   Cookie: cookieStore.toString(),
    // },
    credentials: "include",
    cache: "no-store",
  });
   const response = await axios.get("https://your-django-api.com/api/data", {
     headers: {
       Cookie: `access_token=${token}`,
     },
     withCredentials: true, // Important for cookies
   });

   return response.data;
  return res.json();
}

export default async function User() {
  const data = await getData();
  console.log(data);

  return (
    <main>
      <TagBar />
      <div className="flex">
        <Sidebar />
        <div className="flex-1 min-h-screen pt-6">
          <Header />
          <div className="h-screen p-4">
            <CustomTable />
          </div>
        </div>
      </div>
    </main>
  );
}
